import VisualComponent from "jx/core/comps/VisualComponent";
import Common from "jx/editor/editionitems/Common";
let textInput = `
<div class="form-group row">
   <div class="col-xs-12">
   <div id="id"></div>
        <div id="value">$VALUE</div>
   </div>
 </div>
`;
export default class SimpleText extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        console.log("SPTTTTT");
        console.log(this.data.value);
        console.log(this.data.value.replace("\n", "<br/>"));
        this.commonLib = new Common({ jx: this.jx });
        var item = $(textInput);
        item.prop('id', this.data.id);
        //if (this.data.description == undefined) { item.remove($(item.description_btn)); }
        item.find("#description").html(this.data.description || "");
        // var textarea = item.find("#value")[0];
        // $(textarea).css('height', 'auto').css('height', textarea.clientHeight);
        //  var offset = textarea.offsetHeight - textarea.clientHeight;
        //  var resizeTextarea = function(el) {
        //      $(el).css('height', 'auto').css('height', el.scrollHeight + offset);
        //  };
        //  $(textarea).on('keyup input', function() { resizeTextarea(textarea); }).removeAttr('data-autoresize');
        item.find("#value").prop('readonly', (this.data.editable != undefined) ? !this.data.editable : false);
        item.find("#value").css("word-wrap", "normal");
        item.find("#value").css("font-size", "12px");
        // item.find("#value").html("<pre>"+this.data.value +"</pre>"|| "");
        item.find("#id").html("<b>" + this.data.id + "</b><br><br>" || "");
        item.find("#value").html(this.data.value.replace(new RegExp("\n", "g"), "<br>") || "");
        item[0].addEventListener("change", (event) => { this.controller.onChangeData(event); });
        item.find("#info").click((event) => {
            console.log("info", event.target, item[0].id);
            var panelInfo = item.find("#informationsPanel");
            panelInfo.css("display", (panelInfo.css("display") == "none" ? "inline" : "none"));
        });
        this.item = item;
    }
}
